body {
  color: #333;
  text-align: center;
  background-color: #f0f0f0;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: sans-serif;
  display: flex;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 15px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: calc(100% - 18px);
  margin-top: 10px;
  padding: 8px;
  font-size: 16px;
}

#wallet-info p {
  margin: 5px 0;
}

#wallet-address {
  word-break: break-all;
  font-weight: bold;
}

#tonkey-balance {
  color: green;
  font-weight: bold;
}
